.wrapper {
  padding-left:20px;
}
.title {
  font-size: 20px;
  line-height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  
}
.titleText{
  width:170px;
}
.titleLine {
  display: inline-block;
  margin-left:10px;
  border-bottom:1px #000000 solid;
  width:100%;
  height:10px;
}



.list {
  list-style:none;
  padding:0;
  margin:0;
}

.wrapperItem {
  display:grid;
  grid-template-columns:2fr 4fr;
  text-align:left;
}
.dateStartEnd, .duration{
  text-decoration:underline;
}