.titleWrapper {
    height:70px;
    background: linear-gradient(97.65deg, #BBDDF6 10.57%, #E8F3FB 35.66%, #FEFEFE 51.94%);
    display:flex;
    justify-content: center;
}
.title {
    align-self:center;
    text-transform: uppercase;
    font-size: 36px;
}

.contactsList {
    list-style:none;
    padding:0px;
    margin:0px;
    display:flex;
    flex-direction:column;
    text-align:left;
}

.contactsItem {
    line-height: 6px;
    display: flex;
    align-items: center;
}

.starsList {
    display:flex;
}