.wrapper {
    width:var(--width);
    margin:0 auto;
}

.title {
    margin:0;
    text-align:left;
}
.list {
    padding:0;
    margin:0;
}
.item {
    display:grid;
    grid-template-columns:1fr 1fr;
    align-items:center;
}

.projectImg {
    border-radius:5px;
    padding:0;
    margin:0 auto;
    width:300px;
    height:auto;
    box-shadow:2px 2px 15px rgb(95, 95, 95);
}
.projectImg:hover {
    scale:2;
    transition: scale 1s;
}

.projectDescription {
    text-align:left;
    height:160px;
    overflow:hidden;
    margin-bottom:10px;
}
.projectButtons {
    display:flex;
    justify-content:space-evenly;
    align-items:center;
}

.projectButton {
    display:block;
    padding:10px 15px;
    text-decoration:none;
    background: linear-gradient(170deg, #5fccff,#81b3f6);
    box-shadow:2px 2px 5px rgb(180, 180, 180);
    border-radius:5px;
    color:white;
}
.projectButton:hover {
    display:block;
    padding:10px 15px;
    text-decoration:none;
    background: linear-gradient(170deg, #5fccff,#81b3f6);
    box-shadow:2px 2px 15px rgb(95, 95, 95);
    border-radius:5px;
    color:rgb(0, 0, 0);
}