.wrapper {
  padding-left:20px;
}
.title {
  font-size: 20px;
  line-height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.titleLine {
  display: inline-block;
  margin-left:10px;
  border-bottom:1px #000000 solid;
  width:915px;
  height:10px;
}
.titleText{
  
}
.coursesList {
  list-style:none;
  padding:0;
  margin:0;
}

.placeOfCourses {
  width: 100%;
  display:flex;
  flex-wrap:wrap;
  justify-content: space-between;
  
}
.dateStartEnd, .duration{
  text-decoration:underline;
}

.nameOfSpecialization{
  font-weight:600;
}
.skill{
  
  padding:15px 25px;
  text-decoration:none;
  background: linear-gradient(170deg, #5fccff,#81b3f6);
  box-shadow:2px 2px 5px rgb(180, 180, 180);
  border-radius:5px;
  color:white;
}
